import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import withWidth from '@material-ui/core/withWidth'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import Hidden from '@material-ui/core/Hidden'

import MenuIcon from '@material-ui/icons/Menu'

const styles = theme => ({
  root: {
    zIndex: theme.zIndex.drawer + 1
  },
  header: {
    background: 'linear-gradient(to right, red, orange, red)'
  },
  viewOnGithub: {
    position: 'fixed',
    top: 0,
    right: 0,
    height: 64,
    zIndex: 1,
    [theme.breakpoints.only('xs')]: {
      height: 56
    }
  }
})

const AppHeaderContent = (props) => {
  const { classes, isMobileOpen, toggleMobileDrawer, width } = props

  return (
    <AppBar position='absolute' className={classes.root}>
      <Toolbar
        disableGutters={width === 'xs' && !isMobileOpen}
        className={classes.header}>
        <Typography variant='title' color='inherit' noWrap>
          <Hidden mdUp={width !== 'xs'}>
            <IconButton
              color='inherit'
              aria-label='open drawer'
              onClick={() => toggleMobileDrawer()}>
              <MenuIcon />
            </IconButton>
          </Hidden>
          CH05 Comic Viewer
        </Typography>
        <a href='https://github.com/sky172839465/the-f2e-ch05-comic-viewer' target='block'>
          <img
            className={classes.viewOnGithub}
            src={`https://sky172839465.github.io/the-f2e/img/view-on-github.black.png?now=${Date.now()}`}
            alt='View on github' />
        </a>
      </Toolbar>
    </AppBar>
  )
}

AppHeaderContent.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(withWidth()(AppHeaderContent))



