import React from 'react'
import Loadable from 'react-loadable'

import AppLoading from './container/AppLoading'
import SearchIcon from '@material-ui/icons/Search'
import FavoriteIcon from '@material-ui/icons/Favorite'
import HistoryIcon from '@material-ui/icons/History'
import PlaylistAddIcon from '@material-ui/icons/PlaylistAdd'

const lazyloadComponent = (componentName) => {
  return Loadable({
    loader: () => import(`./container/${componentName}`),
    loading: AppLoading
  })
}

const RouteConfig = [
  {
    page: 'Search',
    path: '/search',
    component: lazyloadComponent('Search'),
    icon: <SearchIcon />,
    visiable: true
  }, {
    page: 'Favorite',
    path: '/favorite',
    component: lazyloadComponent('Favorite'),
    icon: <FavoriteIcon />,
    visiable: true
  }, {
    page: 'History',
    path: '/history',
    component: lazyloadComponent('History'),
    icon: <HistoryIcon />,
    visiable: true
  }, {
    page: 'Add Comic',
    path: '/add',
    component: lazyloadComponent('Upload'),
    icon: <PlaylistAddIcon />,
    visiable: true
  }, {
    page: 'detail',
    path: '/detail/:comicKey',
    component: lazyloadComponent('Detail'),
    icon: null,
    visiable: false
  }, {
    page: '404',
    path: '/404',
    component: lazyloadComponent('NoFound'),
    icon: null,
    visiable: false
  }
]

export default RouteConfig



