import * as actionTypes from './actionTypes'

export const setDataLoaded = (isDataLoaded) => {
  return {
    type: actionTypes.SET_DATA_LOADED,
    payload: {
      isDataLoaded
    }
  }
}

export const setCurrentRoute = (currentRoute) => {
  return {
    type: actionTypes.SET_CURRENT_ROUTE,
    payload: {currentRoute}
  }
}

export const toggleModal = () => {
  return {
    type: actionTypes.TOGGLE_MODAL
  }
}

export const updateModalType = (modalType) => {
  return {
    type: actionTypes.UPDATE_MODAL_TYPE,
    payload: {
      modalType
    }
  }
}

export const toggleMobileDrawer = () => {
  return {
    type: actionTypes.TOGGLE_MOBILE_DRAWER
  }
}

export const toggleLocalFavorite = (comic) => {
  return {
    type: actionTypes.TOGGLE_LOCAL_FAVORITE,
    payload: {
      comic
    }
  }
}

export const setSelectComic = (selectComic) => {
  return {
    type: actionTypes.SET_SELECT_COMIC,
    payload: {
      selectComic
    }
  }
}

export const setSelectChapter = (selectChapter) => {
  return {
    type: actionTypes.SET_SELECT_CHAPTER,
    payload: {
      selectChapter
    }
  }
}

export const deleteLocalComicItem = (comic) => {
  return {
    type: actionTypes.DELETE_LOCAL_COMIC_ITEM,
    payload: {
      comic
    }
  }
}



