import * as type from './actions/actionTypes'

const initialState = {
  isDataLoaded: true,
  isModalOpen: false,
  isMobileOpen: false,
  userInfo: {},
  comicList: [],
  modalType: '',
  selectComic: {},
  selectChapter: {},
  comicHistoryList: []
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case type.GET_USER_INFO:
      return {
        ...state,
        isDataLoaded: true,
        userInfo: {...action.userInfo}
      }
    case type.SET_DATA_LOADED:
      return {
        ...state,
        isDataLoaded: action.payload.isDataLoaded
      }
    case type.TOGGLE_MODAL:
      return {
        ...state,
        selectSpecList: [],
        selectedSpecList: (state.isModalOpen ? [] : state.selectedSpecList),
        selectProduct: (state.isModalOpen ? {} : state.selectProduct),
        selectChapter: (state.isModalOpen ? {} : state.selectChapter),
        isModalOpen: !state.isModalOpen
      }
    case type.UPDATE_MODAL_TYPE:
      return {
        ...state,
        modalType: action.payload.modalType
      }
    case type.TOGGLE_MOBILE_DRAWER:
      return {
        ...state,
        isMobileOpen: !state.isMobileOpen
      }
    case type.TOGGLE_LOCAL_FAVORITE:
      let newComicList = [...state.comicList]
      let newComicHistoryList = [...state.comicHistoryList]
      newComicList = newComicList.map(comic => {
        if (comic.key === action.payload.comic.key) {
          comic.isLoading = !action.payload.comic.isLoading
        }
        return comic
      })
      newComicHistoryList = newComicHistoryList.map(comic => {
        if (comic.key === action.payload.comic.key) {
          comic.isLoading = !comic.isLoading
        }
        return comic
      })

      return {
        ...state,
        comicList: newComicList,
        comicHistoryList: newComicHistoryList
      }
    case type.GET_COMIC_LIST:
    case type.ADD_NEW_COMIC:
    case type.UPDATE_COMIC_ITEM:
    case type.DELETE_COMIC_ITEM:
      return {
        ...state,
        isDataLoaded: true,
        comicList: action.payload.comicList
      }
    case type.SET_SELECT_COMIC:
      return {
        ...state,
        isModalOpen: false,
        comicList: [],
        selectComic: action.payload.selectComic
      }
    case type.SET_SELECT_CHAPTER:
      return {
        ...state,
        selectChapter: action.payload.selectChapter
      }
    case type.GET_COMIC_HISTORY_LIST:
    case type.SET_COMIC_HISTORY:
      return {
        ...state,
        comicHistoryList: action.payload.comicHistoryList
      }
    case type.DELETE_LOCAL_COMIC_ITEM:
      newComicList = [...state.comicList]
      newComicList = newComicList.map(comic => {
        if (comic.key === action.payload.comic.key) {
          comic.isLoading = !comic.isLoading
        }
        return comic
      })
      return {
        ...state,
        comicList: newComicList
      }
    default:
      return state
  }
}

export default reducer



