import React, { Component } from 'react'
import { HashRouter, Switch, Route, Redirect } from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
// import orange from '@material-ui/core/colors/orange'

import RouteConfig from './RouteConfig'
import AppHeader from './container/Common/AppHeader'
import AppBar from './container/Common/AppBar'
import NoFoundPage from './container/NoFound'

const styles = theme => ({
  root: {
    flexGrow: 1,
    height: '100vh',
    zIndex: 1,
    overflow: 'auto',
    position: 'relative',
    display: 'flex'
    // background: `linear-gradient(to right, ${orange[50]}, ${orange[500]}, ${orange[50]})`
  },
  main: {
    maxWidth: 500
  },
  toolbar: theme.mixins.toolbar
})

class App extends Component {
  render () {
    const {classes} = this.props

    return (
      <HashRouter>
        <div className={classes.root}>
          <AppHeader />
          <AppBar />
          <main className={classes.root}>
            <Grid container spacing={0} justify='center'>
              <Grid item xs={12} sm={7} md={6} className={classes.main}>
                <div className={classes.toolbar} />
                <Switch>
                  <Route exact path='/' render={() => <Redirect to={RouteConfig[0].path} />} />
                  {RouteConfig.map(route => {
                    return <Route path={route.path} component={route.component} key={route.page} />
                  })}
                  <Route component={NoFoundPage} />
                </Switch>
              </Grid>
            </Grid>
          </main>
        </div>
      </HashRouter>
    )
  }
}

export default withStyles(styles)(App)



