import * as actionTypes from './actionTypes'

const API_SERVICE = 'https://us-central1-the-f2e-service.cloudfunctions.net'

export const getGithubInfo = () => {
  return (dispatch) => {
    fetch('https://api.github.com/users/sky172839465')
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_USER_INFO,
          userInfo: data
        })
      })
  }
}

export const getComicList = (query = {}) => {
  const queryString = Object.keys(query).map(key => `${key}=${query[key]}`).join('&')
  return (dispatch) => {
    fetch(`${API_SERVICE}/getComicList?${queryString}`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_COMIC_LIST,
          payload: {
            comicList: data.result
          }
        })
      })
  }
}

export const addNewComic = (comic) => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/addNewComic`, {
      method: 'post',
      body: JSON.stringify({})
    })
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.ADD_NEW_COMIC,
          payload: {
            comicList: data.result
          }
        })
      })
  }
}

export const updateComicItem = (comic, query = {}) => {
  const queryString = Object.keys(query).map(key => `${key}=${query[key]}`).join('&')

  return (dispatch) => {
    fetch(`${API_SERVICE}/updateComicItem?${queryString}`, {
      method: 'post',
      body: JSON.stringify({
        ...comic
      })
    })
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.UPDATE_COMIC_ITEM,
          payload: {
            comicList: data.result
          }
        })
      })
  }
}

export const deleteComicItem = (comic) => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/deleteComicItem`, {
      method: 'post',
      body: JSON.stringify({
        key: comic.key
      })
    })
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.DELETE_COMIC_ITEM,
          payload: {
            comicList: data.result
          }
        })
      })
  }
}

export const getComicHistoryList = () => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/getComicHistoryList`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_COMIC_HISTORY_LIST,
          payload: {
            comicHistoryList: data.result
          }
        })
      })
  }
}

export const setComicHistory = (comic, chapter) => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/setComicHistory`, {
      method: 'post',
      body: JSON.stringify({
        comicKey: comic.key,
        chapterNo: chapter.no
      })
    })
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.SET_COMIC_HISTORY,
          payload: {
            comicHistoryList: data.result
          }
        })
      })
  }
}



