import React, {Component} from 'react'
import {Link, withRouter} from 'react-router-dom'
import { withStyles } from '@material-ui/core/styles'
import RouteConfig from '../../../../RouteConfig'

import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import Typography from '@material-ui/core/Typography'

import orange from '@material-ui/core/colors/orange'

const styles = theme => ({
  root: {
    paddingTop: 0
  },
  onHover: {
    color: orange[200],
    '&:hover': {
      backgroundColor: orange[50]
    }
  },
  orange: {
    color: orange[700]
  }
})

class AppMenu extends Component {
  constructor (props) {
    super(props)
    this.state = {
      currentRoute: this.props.location.pathname
    }
  }

  componentWillUnmount () {
    this.isCanceled = true
  }

  componentDidMount () {
    this.props.history.listen(location => {
      !this.isCanceled && this.setState({currentRoute: location.pathname})
    })
  }

  render () {
    const {classes, toggleMobileDrawer} = this.props
    const {currentRoute} = this.state

    return (
      <List component='nav' className={classes.root}>
        {
          RouteConfig
            .filter(route => route.visiable)
            .map(route => {
              const isCurrentRoute = (currentRoute === route.path)
              return (
                <ListItem
                  button
                  divider
                  key={route.page}
                  component={Link}
                  to={route.path}
                  classes={{root: classes.onHover}}

                  onClick={toggleMobileDrawer}>
                  <ListItemIcon className={isCurrentRoute ? classes.orange : ''}>
                    {route.icon}
                  </ListItemIcon>
                  <Typography variant='subheading' className={isCurrentRoute ? classes.orange : ''}>
                    {route.page}
                  </Typography>
                </ListItem>
              )
            })
        }
      </List>
    )
  }
}

export default withStyles(styles)(withRouter(AppMenu))



